.backdrop {
    position        : fixed;
    top             : 0;
    left            : 0;
    min-width       : 100%;
    min-height      : 100%;
    padding         : 10px 15px;
    background-color: rgba(0, 0, 0, 0.4);
    opacity         : 1;
    overflow-y      : scroll;
    transition: all 800ms linear;

    &.is-hidden {
        visibility    : hidden;
        opacity       : 0;
        pointer-events: none;
        transform: perspective(600px) translateX(50%) rotateY(45deg);
        transition: all 800ms linear;
    }
        @media screen and (max-width: 767px) {
        display: none;
    }
}

.modal-main {
    @media screen and (max-width: 767px) {
    display: none;
}
    width   : 100vw;
    height  : 100vh;
transition: all 800ms linear;
opacity: 1;
transform: perspective(600px) translateX(-100%) rotateY(0deg);
    @media screen and (min-width: 768px) {
        width : 90%;
        left  : 50%;
        height: 50vh;
    }

    @media screen and (min-width: 1280px) {
        width : 900px;
        left  : 50%;
        height: 600px;
    }

    border-radius   : 6px;
    transform       : scale(1) translateX(-50%);
    background-color: $black-text-color;

    & {
        position  : relative;
        padding   : 1em;
        margin    : 0.2em 10px;
        background: #fff;
        box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
        box-shadow: 0 15px 10px -10px rgba(0, 0, 0, 0.5), 0 1px 4px rgba(0, 0, 0, 0.3),
            0 0 40px rgba(0, 0, 0, 0.1) inset;
    }

    &:before,
    &:after {
        content : '';
        position: absolute;
        z-index : -2;
    }

    .modal-main__foto {
        text-align: center;
        width     : 100%;
        height    : 100%;
        padding   : 10px 20px;
    }
}

.modal__button-close {
    position     : absolute;
    top          : 4px;
    right        : 4px;
    width        : 30px;
    height       : 30px;
    border-radius: 50%;
    fill: $accent-color;
    cursor       : pointer;
    opacity      : 0.5;
    border       : 1px solid rgba(0, 0, 0, 0.1);
    box-sizing   : border-box;
    display      : flex;
    align-items  : center;
    transition   : box-shadow 250ms linear, opacity 250ms linear, fill 250ms linear;

    &:hover,
    &:focus {
        box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
            1px 4px 6px rgba(0, 0, 0, 0.16);
        opacity: 1;
    }
}

.modal-main__title {
    @include main-text($fw: 400, $fs: 18px, $lx: 1.5, $color: $bckg-color-roze);
     margin-bottom: 10px;
    text-shadow      : 0px 4px 3px rgba(0, 0, 0, 0.4), 0px 8px 13px rgba(0, 0, 0, 0.1),
        0px 18px 23px rgba(0, 0, 0, 0.1);

    @media screen and (min-width: 768px) {
        @include main-text($fw: 500, $fs: 24px, $lx: 1.5, $color: $bckg-color-roze);
         margin-bottom: 15px;
    }

    @media screen and (min-width: 1280px) {
        @include main-text($fw: 600, $fs: 28px, $lx: 1.5, $color: $bckg-color-roze);
         margin-bottom: 20px;
    }

}

