.form-produc__item {
    margin-bottom: 10px;
}

.form-produc__item-foto {
    margin-bottom: 10px;
    display    : flex;
    align-items: center;
}

.form-produc__label {
    @include main-text($fw: 300, $fs: 12px, $lx: 1.2, $color: $bckg-color-roze);
    display          : block;
    margin-bottom    : 5px;
}

.form-produc__men {
    margin-top: 9px;
}

.form-produc__input {
    width        : 100%;
    border-radius: 5px;
    padding      : 2px 15px;
    border       : 1px solid $accent-color;
    outline      : none;

    &:hover,
    &:focus {
        background-color: #ffc0cb;
    }
}

textarea.form-produc__input {
    height : 40px;
    resize : none;
    padding: 2px 15px;
}

.form-produc__men {
    display: flex;
}

.form-produc__options:nth-child(2) {
    margin-left: 50px;
}

.form__options-input {
    display: none;
}

.form__options-input:checked+.form-produc__options-label::after {
    transform: scale(1);
}

.form-produc__options-label {
    @include main-text($fw: 300, $fs: 12px, $lx: 1.2, $color: $bckg-color-roze);
    display          : inline-flex;
    position         : relative;
    cursor           : pointer;
}

.form-produc__options-label::before {
    content         : '';
    align-self      : flex-start;
    flex            : 0 0 24px;
    height          : 24px;
    background-color: $bckg-color-roze;
    border-radius   : 50%;
    margin-right    : 10px;
    cursor          : pointer;
    margin-top      : -4px;
}

.form-produc__options-label::after {
    content         : '';
    position        : absolute;
    width           : 16px;
    height          : 16px;
    left            : 4px;
    border-radius   : 50%;
    background-color: $accent-color;
    transform       : scale(0);
    transition      : transform 300ms linear;
}

.form-produc__select {
    width        : 100%;
    cursor       : pointer;
    border       : 1px solid $accent-color;
    outline      : none;
    height       : 25px;
    border-radius: 5px;
    padding      : 2px 15px;
}

.file {
    margin-left: 10px;

    @media screen and (min-width: 768px) {
        margin-left: 30px;
    }

    @media screen and (min-width: 1280px) {
        margin-left: 45px;
    }
}

.file__item {
    position: relative;
}

.file__input {
    position : absolute;
    top      : 0;
    left     : 0;
    width    : 100%;
    height   : 100%;
    opacity  : 0;
    font-size: 0;
    cursor   : pointer;
}

.file__button {
    @include main-text($fw: normal, $fs: 14px, $lx: 1.5, $color: $bckg-color-roze );
     display:inline-flex;
    min-height      : 40px;
    border-radius   : 30px;
    justify-content : center;
    align-items     : center;
    background-color: $accent-color;
    padding         : 0 20px;
    box-shadow      : 0px 3px 1px rgba(0, 0, 0, 0.2);
    transition      : box-shadow 250ms linear;

    &:hover,
    &:focus {
        box-shadow: 0px 4px 0px rgba(0, 0, 0, 0.8);
    }
}

.file__preview {
    display  : inline-block;
    max-width: 100px;
    padding  : 10px 0 0 0;
}

.file__preview img {
    max-width: 100%;
}

.checkbox__input {
    display: none;
}

.checkbox__input:checked+.chekcbox__label::after {
    transform: scale(1);
}

.chekcbox__label {
    @include main-text($fw: 300, $fs: 10px, $lx: 1.2, $color: $bckg-color-roze);
    display          : inline-flex;
    align-items      : center;
    position         : relative;
    cursor           : pointer;

    &::before {
        content         : '';
        flex            : 0 0 15px;
        height          : 15px;
        background-color: $bckg-color-roze;
        border-radius   : 4px;
        align-self      : flex-start;
        margin-right    : 10px;
        margin-top      : 5px;
    }

    &::after {
        content         : '';
        position        : absolute;
        width           : 11px;
        height          : 11px;
        background-color: $bckg-color-roze;
        border-radius   : 4px;
        align-self      : flex-start;
        margin-right    : 10px;
        top             : 8px;
        left            : 2px;
        border-radius   : 4px;
        background-color: $accent-color;
        transform       : scale(0);
        transition      : transform 250ms linear;
    }
}

.chekcbox__link {
    @include main-text($fw: 600, $fs: 14px, $lx: 1.5, $color: rgb(61, 61, 245));

    &:hover,
    &:focus {
        text-decoration-line : underline;
        text-decoration-style: dashed;
    }
}

.form-produc__button-box {
    text-align: center;
}

.form-produc__button {
    @include main-text($fw: normal, $fs: 16px, $lx: 1.5, $color: $bckg-color-roze);
    letter-spacing   : 1px;
    background-color : $accent-color;
    border           : none;
    border-radius    : 15px;
    padding          : 5px 20px;
    cursor           : pointer;
    box-shadow       : 0px 3px 1px rgba(0, 0, 0, 0.2);
    transition       : box-shadow 250ms linear;

    &:hover,
    &:focus {
        box-shadow: 0px 4px 0px rgba(0, 0, 0, 0.8);
    }
}